/* Reset some default styling */
body, h1, p, input, textarea, button {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Set basic styling for the entire app */
.App {
  font-family: 'Arial', sans-serif;
  color: white;
  background-color: #1a4731; /* Dark green background */
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Style for the profile container */
.profile-container {
  width: 90%;
  max-width: 500px; /* Optimized for mobile, but caps size for larger screens */
  padding: 20px;
  border-radius: 10px;
  background-color: #2a5a40; /* Slightly lighter green for the form background */
}

/* Input and Textarea styling */
input, textarea {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  background-color: #3d6b52;
  color: white;
}

/* Button styling */
button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #4c7a63;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #5e8a74;
}
